#Work{
    width: 100%;
    overflow: hidden;
    
}
#part1{
    background-color: blanchedalmond;
    height: 700px;
}
#part2{
    overflow: hidden;
}
#transition{
    position: absolute;
    width:100%;
    height:100px;
    background: linear-gradient(to top, black,transparent);
    z-index:2;
    margin-top: -96px;
}
#workContainer{
    position: relative;
    z-index: 7;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
}
#workCard1,
#workCard2,
#workCard3,
#workCard4{
    height: 30vh;
    width:30vw;
    background-color: white;
    border-radius: 2vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
    overflow: hidden;
    position: relative;
}
#workCard1:hover,
#workCard2:hover,
#workCard3:hover,
#workCard4:hover{
    transition: 0.3s ease-in-out;
    box-shadow: 10px 10px rgba(0, 0, 0, 0.5) ;
}
#Work h2{
    color: black;
    display: flex;
    justify-content: center;
    font-size: 1.5vw;
    margin-left: 1vw;
}
#Work h3{
    color: black;
    display: flex;
    justify-content: center;
    font-size: 1vw;
}
#pjImgContainer1,
#pjImgContainer2,
#pjImgContainer3,
#pjImgContainer4{
    height: 20vh;
    width: 25vw;
    position:absolute;
    overflow: hidden;
    border:5px solid black;
    border-radius: 1vw;
    display: flex;
    justify-content: center;
}
#pjImgContainer1 img,
#pjImgContainer2 img,
#pjImgContainer3 img,
#pjImgContainer4 img{
    transform: scale(1.4);
}
#pjImgfixer1,
#pjImgfixer2,
#pjImgfixer3,
#pjImgfixer4{
    width:100%;
    display: flex;
    justify-content: center;
}
#lampContainer{
    position:relative;
    z-index: 3;
    margin-top: -68vh;
    display: flex;
    overflow: hidden;
    width: 100%;
}
#lamp{
    margin-left: 200px;
}
#lamp1,#lamp2,#lamp3,#lamp4,#lamp5,#lamp6,#lamp7{
    margin-left: 150px;
}

.rope{
    height:600px;
    width:10px;
    background-color: black;
  }
.cone{
    height:70px;
    width: 50px;
    transform: perspective(10px) rotateX(3deg);
    background-color: black;
    margin: 0px;
    margin-top: -10px;
    margin-left: -20px;
}
.bulb{
    height: 30px;
    width: 30px;
    background-color: yellow;
    border-radius: 100%;
    margin-left: -10px;
}
.light{
    opacity: 0.3;
    height:600px;
    width: 100px;
    transform: perspective(1vh) rotateX(3deg);
    background-color: transparent;
    margin: 0px;
    margin-top: -225px;
    margin-left: -46px;
}
#rope2{
    height: 900px;
}
#rope3{
    height: 300px;
}
#rope4{
    height: 500px;
}
#rope5{
    height: 650px;
}
#rope6{
    height: 700px;
}
#rope7{
    height:650px;
}
#filler{
    height:676px;
    width:100%;
    background-color: black;
    position:relative;
    z-index: 1;
    margin-top: -957px;
}
a{
    text-decoration: none;
    
}
